import { Component } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';

import { VideocallPage } from '../videocall/videocall';
import { VoicecallPage } from '../voicecall/voicecall';
import { ProfileinfoPage } from '../profileinfo/profileinfo';
import { NotificationsettingPage } from '../notificationsetting/notificationsetting';

@Component({
  selector: 'page-chatting',
  templateUrl: 'chatting.html'
})
export class ChattingPage {
  private viewType: string;

  constructor(public navCtrl: NavController, public modalCtrl: ModalController) {

  }
  setViewType(vt) {
    this.viewType = vt;
  }

  profileinfo() {
    this.navCtrl.push(ProfileinfoPage)
  }

  videocall() {
    let modal = this.modalCtrl.create(VideocallPage);
    modal.present();
  }
  voicecall() {
    let modal = this.modalCtrl.create(VoicecallPage);
    modal.present();
  }

  orderinfo() {
    this.navCtrl.push(NotificationsettingPage)
  }
}



