import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { CalllogsPage } from '../pages/calllogs/calllogs';
import { ChatlistPage } from '../pages/chatlist/chatlist';
import { ChatsettingPage } from '../pages/chatsetting/chatsetting';
import { ChattingPage } from '../pages/chatting/chatting';
import { CreategroupPage } from '../pages/creategroup/creategroup';
import { CreategroupinfoPage } from '../pages/creategroupinfo/creategroupinfo';
import { GroupchattingPage } from '../pages/groupchatting/groupchatting';
import { GroupinfoPage } from '../pages/groupinfo/groupinfo';
import { GrouplistPage } from '../pages/grouplist/grouplist';
import { MyprofilePage } from '../pages/myprofile/myprofile';
import { MorePage } from '../pages/more/more';
import { NewchatPage } from '../pages/newchat/newchat';
import { NotificationsettingPage } from '../pages/notificationsetting/notificationsetting';
import { PrivacyPage } from '../pages/privacy/privacy';
import { ProfileinfoPage } from '../pages/profileinfo/profileinfo';
import { ProfilepicturePage } from '../pages/profilepicture/profilepicture';
import { SetprofilePage } from '../pages/setprofile/setprofile';
import { SigninPage } from '../pages/signin/signin';
import { TabsPage } from '../pages/tabs/tabs';
import { VerificationPage } from '../pages/verification/verification';
import { VideocallPage } from '../pages/videocall/videocall';
import { VoicecallPage } from '../pages/voicecall/voicecall';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,  
    CalllogsPage,
    ChatlistPage,
    ChatsettingPage,
    ChattingPage,
    CreategroupPage,
    CreategroupinfoPage,
    GroupchattingPage,
    GroupinfoPage,
    GrouplistPage,  
    MyprofilePage,
    MorePage,
    NewchatPage,
    NotificationsettingPage,
    PrivacyPage,
    ProfileinfoPage,
    ProfilepicturePage,
    SetprofilePage,
    SigninPage,
    TabsPage,
    VerificationPage,
    VideocallPage,
    VoicecallPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient]
      }
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,   
    CalllogsPage,
    ChatlistPage,
    ChatsettingPage,
    ChattingPage,
    CreategroupPage,
    CreategroupinfoPage,
    GroupchattingPage,
    GroupinfoPage,
    GrouplistPage,   
    MyprofilePage,
    MorePage,
    NewchatPage,
    NotificationsettingPage,
    PrivacyPage,
    ProfileinfoPage,
    ProfilepicturePage,
    SetprofilePage,
    SigninPage,
    TabsPage,
    VerificationPage,
    VideocallPage,
    VoicecallPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



