import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { PrivacyPage } from '../privacy/privacy';
// import { SetProfile } from '../setprofile/setprofile';
import { ChatsettingPage } from '../chatsetting/chatsetting';
import { NotificationsettingPage } from '../notificationsetting/notificationsetting';
import { MyprofilePage } from '../myprofile/myprofile';
import { GrouplistPage } from '../grouplist/grouplist';
@Component({
      selector: 'page-more',
      templateUrl: 'more.html'
})
export class MorePage {

      constructor(public navCtrl: NavController) {

      }

      privacy() {
            this.navCtrl.push(PrivacyPage)
      }
      chatsetting() {
            this.navCtrl.push(ChatsettingPage)
      }
      notificationsetting() {
            this.navCtrl.push(NotificationsettingPage)
      }
      myorder() {
            this.navCtrl.push(GrouplistPage)
      }
      myprofile() {
            this.navCtrl.push(MyprofilePage)
      }


}



